/*
* Provides rendering of a table which is using the given FlexibleTableHeader set in
* order to tabulate the given data. As per definition of earch header component,
* a column could be hidden, sortable, or draggable. Each table row can expand/collapse
* or respond to a click action.
*/
import {
    Component,
	Input,
	Output,
	ViewChild,
	ViewContainerRef,
	OnInit,
	EventEmitter,
	ElementRef
} from '@angular/core';

import { DropEvent, DragEvent } from 'drag-enabled';

export interface FlexibleTableHeader {
	key: string,
	value: string,
	present: boolean,
	width?: string,
	format?: string,
	dragable?: boolean
	sortable?: boolean,
	class?:string,
	locked?:boolean,
	ascending?: boolean,
	descending?: boolean
}

@Component({
	selector: 'table-view',
	template: `
<table [class]="tableClass"  #flexible>
    <caption *ngIf="caption" [textContent]="caption"></caption>
    <thead>
        <tr>
            <th scope="col" *ngIf="enableIndexing" id="indexable" class="indexable"></th>
            <th scope="col" *ngFor="let header of headers"
                [dragEnabled]="dragEnabled.bind(this)"
                [dropEnabled]="dropEnabled.bind(this)"
                [medium]="header"
                (onDragStart)="onDragStart($event)"
                (onDragEnd)="onDragEnd($event)"
                (onDrop)="onDrop($event)"
                [id]="header.key"
                [attr.width]="header.width ? header.width : null" 
                [attr.tabindex]="header.sortable ? 0 : -1"
                (keydown)="keydown($event, th)" (click)="sort(header, icon)">
                <span *ngIf="header.sortable" class="off-screen"  [textContent]="vocabulary.clickSort"></span>
                <span class="locker icon fa" #locker
                        *ngIf="lockable && (headers.length > 1 || header.locked)"
                        tabindex="0"
                        title="lock/unlock this column"
                        (keydown)="keydown($event, locker)" (click)="lock(header, $event)"
                        [class.fa-lock]="header.locked"
                        [class.fa-unlock]="!header.locked"></span>
                <span class="title"
                        [class.dragable]="header.dragable"
                        [textContent]="header.value"></span>
                <span class="icon fa" #icon
                        [class.fa-sort]="header.sortable"
                        [class.fa-sort-asc]="header.assending"
                        [class.fa-sort-desc]="header.desending"></span>
            </th>
            <th scope="col" *ngIf="action" id="actionable" class="actionable"></th>
        </tr>
    </thead>
    <tbody>
        <ng-template ngFor let-item [ngForOf]="items" let-i="index">
            <tr *ngIf="i >= pageInfo.from && i <= pageInfo.to "
                (click)="actionClick($event, item)"
                (mouseover)="hover(item, true)"
                (mouseout)="hover(item, false)"
                [class.pointer]="action"
                [class.hover]="item.hover"
                [class.expanded]="item.expanded"
                [class.odd]="i%2">
                <td scope="row" class="index" *ngIf="enableIndexing">{{i + 1}}</td>
                <td scope="row" 
                    *ngFor="let header of headers" 
                    [attr.data-label]="header.value" 
                    [intoName]="header.value"
                    [intoId]="header.key + '-' + i"
                    [into]="header.format"
                    [rawContent]="cellContent(item, header)"
                    [onComponentChange]="onTableCellEdit.bind(this)"></td>
                <td scope="row" *ngIf="action">
                    <a class="actionable"
                        *ngIf="expandable(item, true)"
                        tabindex="0"
                        role="button"
                        style="cursor:pointer"
                        [class.expanded]="item.expanded" #clicker
                        (keydown)="keydown($event, clicker)" (click)="actionClick($event, item)">
                        <span
                            class="icon fa"
                            [class.fa-angle-right]="!rowDetailer"
                            [class.fa-minus-square-o]="rowDetailer && item.expanded"
                            [class.fa-plus-square-o]="rowDetailer && !item.expanded"
                            aria-hidden="true"></span>
                        <span class="off-screen" [textContent]="offScreenMessage(item)"></span>
                    </a>
                </td>
            </tr>
            <tr *ngIf="rowDetailer && item.expanded" class="detail" [class.odd]="i%2">
                <td scope="row" class="index" *ngIf="enableIndexing"></td>
                <td [attr.colspan]="columnsCount()">
                    <ng-container [ngTemplateOutlet]="rowDetailer" [ngTemplateOutletContext]="rowDetailerContext(item)"></ng-container>
                </td>
            </tr>
        </ng-template>
    </tbody>
</table>
`,
	styles: [`:host{display:inline-block!important;width:100%;position:relative;margin:0 auto;border-spacing:0;border-collapse:collapse}:host .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host table{margin:1rem auto;padding:0;width:100%;table-layout:fixed;max-width:99%;background-color:transparent;border-collapse:collapse}:host table caption{background-color:#c3e5e2;border-radius:2px;color:#1b1b1b;caption-side:top;font-size:14px;padding:5px 6px;margin-bottom:15px;text-align:left}:host table thead{border-top:1px solid #bbb;border-bottom:1px solid #bbb;background-color:#eee}:host table tr{border:0}:host table tr.expanded td{font-weight:700}:host table td{padding-left:3px}:host table td:first-child{padding-left:5px}:host table td .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host table td ::ng-deep img{height:24px}:host table td.index{background-color:#eee;border-right:1px solid #bbb}:host table th{cursor:default;-webkit-user-select:none;-moz-user-select:none;-o-user-select:none;-ms-user-select:none;user-select:none;height:24px;position:relative;white-space:nowrap;font-weight:400;text-transform:uppercase;font-size:14px;padding-top:6px;padding-bottom:6px;text-align:left}:host table th.drag-over{background-color:#9b9b9b}:host table th.drag-over .icon,:host table th.drag-over .title{color:#eee}:host table th:first-child{padding-left:5px}:host table th.ascending,:host table th.descending,:host table th.sortable{cursor:pointer;height:12px}:host table th.indexable{width:33px}:host table th.actionable{width:24px}:host table th .title{color:#254a4d;display:inline-block;height:20px;white-space:nowrap}:host table th .dragable{cursor:move}:host table th .icon{width:22px;display:inline-block;height:20px;color:#254a4d}:host .fa.fa-angle-right{font-size:18px}table tr.expanded td{border-bottom:0}table tr.detail td{border-top:0;cursor:default}table tr.expanded td a.expanded{background-position:right 2px}table tbody tr.hover,table tbody tr:hover{background-color:#ffeed2}table tbody tr.detail.hover,table tbody tr.detail.hover td table thead tr,table tbody tr.detail:hover,table tbody tr.detail:hover td table thead tr{background-color:inherit}table tr td a.actionable{display:inline-table;height:32px;vertical-align:middle;width:25px;line-height:30px;color:#254a4d}table tbody tr.detail.hover td:last-child,table tbody tr.detail:hover td:last-child{border-right:0}table tbody tr.detail.hover td:first-child,table tbody tr.detail:hover td:first-child{border-left:0}table tr td{border-bottom:1px solid #b1b3b3;color:#254a5d;font-size:15px;text-transform:capitalize}table tbody tr.pointer{cursor:pointer}table.alert-danger{border:0}table.alert-danger caption{background-color:transparent;font-weight:700;margin-bottom:0}table.alert-danger td{border-bottom:0;display:block}table.alert-danger td:first-child{padding-left:0}table.alert-danger td:last-child{border-bottom:0}table.alert-danger td:before{content:attr(data-label);float:left;font-weight:700;text-transform:uppercase;width:20%}table.alert-danger td a span.icon{width:100%}table.alert-danger thead{border:none;clip:rect(0 0 0 0);height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}table.alert-danger tr{border:2px solid #fff;display:block;margin-bottom:.625em;padding:5px;border-radius:5px}table.alert-danger tr th.actionable{width:inherit}table.alert-danger tr td{border-bottom:0}@media screen and (max-width:600px){table{border:0}table td{border-bottom:0;display:block;text-align:right}table td:first-child{padding-left:0}table td:last-child{border-bottom:0}table td:before{content:attr(data-label);float:left;font-weight:700;text-transform:uppercase}table td a span.icon{width:100%}table thead{border:none;clip:rect(0 0 0 0);height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}table tr{border-bottom:3px solid #ddd;display:block;margin-bottom:.625em}table tr th.actionable{width:inherit}table tr td{border-bottom:0}table.alert-danger td:before{width:inherit}}`]
})
export class TableViewComponent implements OnInit {
	private registeredHeaders = [];
    dragging = false;

    @Input("vocabulary")
    public vocabulary = {
		configureTable: "Configure Table",
		configureColumns: "Configure Columns",
		clickSort: "Click to Sort",
		setSize: "Set Size",
		firstPage: "First",
		lastPage: "Last",
		previousPage: "Previous"
	};

	@Input("lockable")
	lockable:boolean;

	@Input("caption")
    public caption: string;

    @Input("action")
    public action: string;

    @Input("pageInfo")
    public pageInfo;

    @Input("actionKeys")
    public actionKeys;

    @Input("tableClass")
    public tableClass = 'default-flexible-table';

	@Input("headers")
	public headers: any[];

	@Input("items")
	public items: any[];

	@Input("tableInfo")
	public tableInfo: any;

    @Input("enableIndexing")
    public enableIndexing: boolean;

    @Input("rowDetailer")
    public rowDetailer: any;

    @Input("expandable")
    public expandable: any;

    @Input("expandIf")
    public expandIf: boolean;

    @Input("rowDetailerHeaders")
    public rowDetailerHeaders: any;

	@Output('onaction')
	private onaction = new EventEmitter();

	@Output('onchange')
	private onchange = new EventEmitter();

	@ViewChild('flexible', {read: ViewContainerRef}) private table: ViewContainerRef;

    constructor(public el:ElementRef) {}


	private findColumnWithID(id: string) {
        const list = this.headerColumnElements();
		let column = null;
		for (let i = 0; i < list.length; i++) {
			if (list[i].getAttribute("id") === id) {
				column = list[i];
				break;
			}
		}
		return column;
	}

	private swapColumns(source: any, destination: any) {

		if (source.node.parentNode === destination.node.parentNode) {
			const srcIndex = this.getColumnIndex(source.medium.key);
			const desIndex = this.getColumnIndex(destination.medium.key);
			if (srcIndex < 0 || desIndex < 0) {
				console.log("invalid drop id", source.medium.key, destination.medium.key);
				return;
			}
			const sobj = this.headers[srcIndex];
			this.headers[srcIndex] = this.headers[desIndex];
			this.headers[desIndex] = sobj;
	
			for (let i = 0; i < this.items.length; i++) {
				const row = this.items[i];
				const sobji = row[srcIndex];
				row[srcIndex] = row[desIndex];
				row[desIndex] = sobji;
			}	
		//	this.onchange.emit(this.headers);
		} else if (source.medium.locked || destination.medium.locked) {
			source.medium.locked = !source.medium.locked;
			destination.medium.locked = !destination.medium.locked;
			this.onchange.emit(this.headers);
		}
	}

	private getColumnIndex(id: string) {
		let index = -1;
		for (let i = 0; i < this.headers.length; i++) {
			if (this.headers[i].key === id) {
				index = i;
				break;
			}
		}
		return index;
	}
	private itemValue(item, hpath) {
		let subitem = item;
		hpath.map( (subkey) => {
			if (subitem) {
				subitem = subitem[subkey] ? subitem[subkey] : undefined;
			}
		})
		return subitem === undefined || subitem === null || subitem === "null" ? "" : subitem;
	}

	lock(header: FlexibleTableHeader, event) {
        event.stopPropagation();	
        event.preventDefault();
		header.locked = !header.locked;
		this.onchange.emit(this.headers);
	}
	sort(header: FlexibleTableHeader, icon) {
		if (header.sortable) {
			for (let i = 0; i < this.headers.length ; i++) {
                const h = this.headers[i];

                if (h.key !== header.key) {
					const item = this.findColumnWithID(h.key);

					if (item) {
						item.classList.remove("ascending");
						item.classList.remove("descending");
						item.classList.add("sortable");
					}
                    h.descending = false;
                    h.ascending = false;
				}
			}
            icon.classList.remove("fa-sort");
			if (header.ascending || (!header.ascending && !header.descending)) {
				header.descending = true;
				header.ascending = false;
				icon.classList.remove("fa-sort-asc");
				icon.classList.add("fa-sort-desc");
			} else {
				header.descending = false;
				header.ascending = true;
				icon.classList.remove("fa-sort-desc");
				icon.classList.add("fa-sort-asc");
			}
			const hpath = header.key.split(".");
			this.items.sort((a, b) => {
				const v1 = this.itemValue(a, hpath);
				const v2 = this.itemValue(b, hpath);

				if (header.ascending) {
					return v1 > v2 ? 1 : -1;
				}
				return v1 < v2 ? 1 : -1;
			});
		}
	}

	offsetWidth() {
		return this.table.element.nativeElement.offsetWidth;
	}

	ngOnInit() {
		if (!this.headers) {
			this.headers = [];
        }
        if (this.actionKeys) {
            this.actionKeys = this.actionKeys.split(",");
		}
		if (!this.rowDetailer && this.expandable) {
			this.rowDetailer = function(item) {
				return {data: item, headers: []};
			};
		}
		if (!this.expandable) {
			this.expandable = function(item, showIcon) {return showIcon};
		}
		if (!this.rowDetailerHeaders) {
			this.rowDetailerHeaders = (item) => [];
		}
	}

    headerColumnElements() {
		return this.table.element.nativeElement.children ?
				this.table.element.nativeElement.children[1].children[0].children : [];
    }

	headerById(id) {
		let h;
		for (const i in this.headers) {
			if (this.headers[i].key === id) {
				h = this.headers[i];
				break;
			}
		}
		return h;
	}

    columnsCount() {
		let count = 0;
		this.headers.map( (item) => {
            if (item.present) {
                count++;
            }
		});
        if (this.action) {
            count++;
        }
        return count;
	}
	hover(item, flag) {
		if (flag) {
			item.hover = true;
		} else {
			delete item.hover;
		}
	}

    keydown(event, item) {
        const code = event.which;
        if ((code === 13) || (code === 32)) {
			item.click();
		}
    }
    offScreenMessage(item) {
        let message: string = this.action;
        this.actionKeys.map((key) => { message = message.replace(key, item[key.substring(1, key.length - 1)]); })
        return message;
    }

    cellContent(item, header) {
		let content = this.itemValue(item, header.key.split("."));
        return (content !== undefined && content != null) ? content : '&nbsp;';
	}

	rowDetailerContext(item) {
		return {
			data: item,
			tableInfo: this.tableInfo,
			headers: this.rowDetailerHeaders(item)
		};
	}

	actionClick(event, item: any) {
		event.stopPropagation();
        if (this.rowDetailer && (this.expandIf || this.expandable(item, false)) ) {
            if (item.expanded) {
                delete item.expanded;
            } else {
                item.expanded = true;
            }
        } else {
            this.onaction.emit(item);
		}
		return false;
	}

	onTableCellEdit(event) {
		const id = event.id.split("-");
		const name = event.name;
		const value= event.value;
		const item = this.items[parseInt(id[1])];

		if (item) {
			const list = id[0].split(".");
			let subitem = item[list[0]];
			for(let i = 1; i < (list.length - 1); i++) {
				subitem = subitem[list[i]]
			}
			if (subitem && list.length > 1){
				subitem[list[list.length - 1]] = value;
			}
		}
    }

	dragEnabled(event: DragEvent) {
		return event.medium.dragable;
	}
	dropEnabled(event: DropEvent) {
		return event.destination.medium.dragable;
	}
	onDragStart(event: DragEvent){
//        this.dragging = true;
	}
	onDragEnd(event: DragEvent){
 //       this.dragging = false;
	}
	onDrop(event:DropEvent){
		this.swapColumns(event.source, event.destination);
	}
}
